@charset "UTF-8";
/* CSS Document */

/* Landing Oage */

.landing-read-our-guide .card .guide-content ul {
    text-align: left;
    padding-left: 20px;
    margin-bottom: 2rem;
    list-style-type: disc;
}


/* Add new css */

.card-body h4,
.card-body h5 {
    margin-bottom: .5rem;
}



/* Global */
body {
    margin: 0;
    font-family: "SofiaPro-Regular", sans-serif;
    font-size: 0.875rem;
    font-weight: 400;
    line-height: 1.5;
    color: #1b1b1b;
    text-align: left;
    background-color: #fff;
}

header a.nav-link.dropdown-toggle::after {
  content: "";
  border: unset;
  background-image: url(/images/icons/chevron-down.svg);
  background-position: center;
  position: relative;
  background-size: contain;
  width: 8px;
  top: 3px;
  height: 8px;
  background-repeat: no-repeat;
}

/* Note: Change arrow to blue */


